@charset "UTF-8";
body {
  font-family: "";
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

@media screen and (min-width: 768px) {
  .box-image-left {
    display: grid;
    grid-template-columns: 1fr 1fr 10%;
    align-items: center;
  }
}

.box-image-left > h2 {
  background: rgb(255, 187, 28);
  text-align: center;
  padding: 0.625rem 1.25rem;
}
@media screen and (min-width: 768px) {
  .box-image-left > h2 {
    display: none;
  }
}

.box-image-left > ul > li > h2 {
  display: none;
}
@media screen and (min-width: 768px) {
  .box-image-left > ul > li > h2 {
    display: block;
  }
}

.box-image-left > ul {
  background: rgb(255, 255, 255);
  padding: 1.25rem;
}
@media screen and (min-width: 768px) {
  .box-image-left > span {
    height: 100%;
    background: rgb(235, 206, 108);
  }
}

.box-image-left > .bottom {
  display: none;
}
@media screen and (min-width: 768px) {
  .box-image-left > .bottom {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .box-image-right {
    display: grid;
    grid-template-columns: 10% 1fr 1fr;
    align-items: center;
  }
}

.box-image-right > h2 {
  background: rgb(255, 187, 28);
  text-align: center;
  padding: 0.625rem 1.25rem;
}
@media screen and (min-width: 768px) {
  .box-image-right > h2 {
    display: none;
  }
}

.box-image-right > ul > li > h2 {
  display: none;
}
@media screen and (min-width: 768px) {
  .box-image-right > ul > li > h2 {
    display: block;
  }
}

.box-image-right > ul {
  background: rgb(255, 255, 255);
  padding: 1.25rem;
}
@media screen and (min-width: 768px) {
  .box-image-right > span {
    height: 100%;
    background: rgb(235, 206, 108);
  }
}

@media screen and (min-width: 768px) {
  .box-image-right > .up {
    display: none;
  }
}

.box-image-right > .bottom {
  display: none;
}
@media screen and (min-width: 768px) {
  .box-image-right > .bottom {
    display: block;
  }
}